import navtop from "../../components/nav_top/nav_top.vue"
export default {
	data() {
		return {
			navtop: {
						title: '四川弘浩物流',
						color: 'black'
					},
					number:'',
		}
	},
	components: {
		navtop
	},
	created() {
		
		this.isBarHeight999().then((isTemp)=>{
			this.barHeight = isTemp
				if(this.barHeight.statusBarHeight > 20){
					this.touheight = 20
				}else{
					this.touheight = 5
				}
		})
	},
	methods: {
		tocall(tel){
			uni.makePhoneCall({
				phoneNumber:tel,
				success: (res) => {
				},
				fail: (res) => {
					console.log(res);
					uni.showToast({
					    title: "拨打失败，请手动拨打电话",
					    icon: "none"
					})
				}
			})
		},
		tosearchlist(){
			console.log(1);
			if(this.number==''){
				uni.showToast({
				    title: "订单号/货号为空",
				    icon: "none"
				})
			}else{
				
				uni.request({
					url: 'http://hhwl.com/wxapp/login/getHh',
					method: 'POST',
					data: {number:this.number,type:1},
					success: res => {
						console.log(res)
						if(res.data.data.result==1){
							uni.showToast({
								title:res.data.data.msg,
								icon: "none"
							})
						}else if(res.data.data.result==0){
							uni.showLoading({
								title:'跳转中....'
							})
							setTimeout(function() {
							  uni.hideLoading()
							}, 10);
							uni.navigateTo({
								url:`../searchlist/searchlist?number=`+this.number
							})
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}
			
			
		},
		search(){
			
		}
		
	}
}
